import './App.css';
import React from 'react';

function App() {
  const [bets, setBets] = React.useState(null)

  React.useEffect(() => {
    async function getBets() {
      const res = await fetch(
        'https://actions.d-e102.com/hook/1ecbea10-f9b6-4590-99c8-03f7fdfba55b'
        ,{
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
        },
          body: JSON.stringify({userId: 'dXNlci1jYWJhNTZlYi1iYmE3LTRkY2UtYmM5Mi1lOTAzZmUzYzIxZjk'})
        })
      setBets(await res.text())
    }
    getBets()
  }, [])
  function createMarkup() {
    return {__html: bets || ''};
  }
  return (
    <div className="App">
      <header className="App-header">
      <h3>Bets</h3>
      </header>
      <div dangerouslySetInnerHTML={createMarkup()} />;
    </div>
  );
}

export default App;
